<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <slot />
      </v-col>

      <feed-card
        v-for="(article, i) in paginatedArticles"
        :key="article.id"
        :size="layout[i]"
        :value="article"
      />
    </v-row>

    <v-row align="center">
      <v-col cols="3">
        <base-btn
          v-if="page !== 1"
          class="ml-0"
          square
          title="Previous page"
          @click="page--"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </base-btn>
      </v-col>

      <v-col
        class="text-center subheading"
        cols="6"
		v-if="pages > 1 && page < pages"
      >
        PAGE {{ page }} OF {{ pages }}
      </v-col>

      <v-col
        class="text-right"
        cols="3"
      >
        <base-btn
          v-if="pages > 1 && page < pages"
          class="mr-0"
          square
          title="Next page"
          @click="page++"
        >
          <v-icon>mdi-chevron-right</v-icon>
        </base-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  // Utilities
  export default {
    name: 'Feed',
	props: {
		articles: {
		type: Array
		},
		category:{
			type: String
		}
	},

    components: {
      FeedCard: () => import('@/components/FeedCard'),
    },

    data: () => ({
      page: 1,
    }),

    computed: {

	  layout(){
		  return this.category== 'guitar' ? [3, 3, 3, 3, 3, 3, 3, 3, 3,3,3,3] : [2, 2, 2, 2, 2, 2, 2, 2, 2];
	  },
      pages () {
        return this.category== 'guitar' ? Math.ceil(this.articles.length / 12) : Math.ceil(this.articles.length / 9)
      },
      paginatedArticles () {

        let start = (this.page - 1);
        let stop = this.page;
		if(this.category== 'guitar'){
			start = start * 12;
			stop = stop * 12;
		}else{
			start = start * 9;
			stop = stop * 9;
		}
        return this.articles.slice(start, stop)
      },
    },

    watch: {
      page () {
        window.scrollTo(0, 0)
      },
    },
  }
</script>
